export default function LeagueTable({ teams, showGoals }) {
  return (
    <table>
      <thead>
        <tr>
          <th>拽</th>
          <th>拽爪</th>
          <th>砖拽</th>
          {showGoals && <th>驻专砖</th>}
          <th>拽转</th>
        </tr>
      </thead>
      <tbody>
        {teams.map((t, i) => {
          return (
            <tr key={t.key}>
              <td>{i + 1}</td>
              <td>{t.displayName}</td>
              <td className={` ${t.games === 37 ? "done" : ""} `}>{t.games}</td>
              {showGoals && <td className="gf">{t.goalDiffrence}</td>}
              <td>{t.points}</td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
}
