import "./styles.scss";
import { useEffect, useState } from "react";
import Switch from "react-switch";

import {
  initialTeams,
  initialFixtures,
  currentVersion,
  resultsTypes
} from "./constans";

import {
  calcuateTeamsPointes,
  minifyJson,
  confetti,
  trackEvent,
  randomGoals,
  getDataFromClient,
  mergeFixtures,
  getShareLink,
  copyToClipboard
} from "./utils";
import LeagueTable from "./components/LeagueTable";
import Fixtures from "./components/Fixtures";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export default function App() {
  const [fixtures, setFixtures] = useState([...initialFixtures]);
  const [teams, setTeams] = useState(
    calcuateTeamsPointes(initialTeams, [...initialFixtures])
  );

  const [showGoals, setShowGoals] = useState(false);

  useEffect(() => {
    if (fixtures.every((f) => f.result)) {
      const hptPosition = teams.findIndex((t) => t.key === "hpt") + 1;
      trackEvent({
        action: "complete",
        category: "hpt",
        label: hptPosition
      });
      trackEvent({
        action: "complete",
        category: "complete",
        label: fixtures.reduce((a, v) => a.concat(v.result), "")
      });
      if (hptPosition === 1 || hptPosition === 2) {
        confetti({
          particleCount: 750,
          spread: 360,
          colors: ["#89CFF0", "#0047AB", "#3F00FF", "#4169E1"]
        });
      } else {
        confetti && confetti.reset();
      }
    }
  }, [teams, fixtures]);

  useEffect(() => {
    setTeams(calcuateTeamsPointes(initialTeams, fixtures));
  }, [fixtures]);

  useEffect(() => {
    const clientFixtures = getDataFromClient();
    setFixtures(mergeFixtures(fixtures, clientFixtures));
  }, []);

  const applyFixtureResult = (
    changedFixtureIdx,
    result,
    homeGoals,
    awayGoals
  ) => {
    const newFixtures = [...fixtures];
    const idx = newFixtures.findIndex(
      (f) => `${f.home}/${f.away}` === changedFixtureIdx
    );

    const mutatedResult =
      result === newFixtures[idx].result && result === resultsTypes.TIE
        ? undefined
        : result;

    newFixtures[idx] = {
      ...newFixtures[idx],
      result: mutatedResult,
      homeGoals,
      awayGoals
    };
    setFixtures(newFixtures);

    const minified = minifyJson(newFixtures);
    window.localStorage.setItem("fixtures", minified);
    window.localStorage.setItem("version", currentVersion);
  };

  const shareLink = getShareLink(fixtures);

  const reset = () => {
    trackEvent({ action: "click", category: "button", label: "reset" });
    setFixtures(initialFixtures);
  };

  const copyShareLink = () => {
    trackEvent({ action: "click", category: "button", label: "share" });
    copyToClipboard(shareLink);
  };

  const shuffle = () => {
    trackEvent({ action: "click", category: "button", label: "shuffle" });
    const newFixtures = fixtures.map((f) => {
      const homeGoals = randomGoals();
      const awayGoals = randomGoals();
      const result =
        homeGoals > awayGoals
          ? resultsTypes.HOME
          : homeGoals < awayGoals
          ? resultsTypes.AWAY
          : resultsTypes.TIE;
      return { ...f, result, homeGoals, awayGoals };
    });
    setFixtures(newFixtures);
  };

  const toggleGoals = (checked) => {
    trackEvent({
      action: "click",
      category: "goals",
      label: checked.toString()
    });
    setShowGoals(checked);
  };

  return (
    <div className="App">
      <div id="table">
        <LeagueTable teams={teams} showGoals={showGoals} />
      </div>
      <button onClick={copyShareLink}>砖转祝 爪 </button>
      <button onClick={reset}>驻住</button>
      <button onClick={shuffle}>拽专</button>
      <div>
        <label>
          <Switch onChange={toggleGoals} checked={showGoals} />
          驻专砖 砖注专
        </label>
      </div>
      <div id="fixtures">
        <Fixtures
          fixtures={fixtures}
          applyFn={applyFixtureResult}
          showGoals={showGoals}
        />
      </div>
      <ToastContainer />
    </div>
  );
}
